<template>
  <div class="toggle">
    <label class="label">
      {{ label }}
    </label>
    <div class="btn-group btn-group-toggle" data-toggle="buttons">
      <label class="btn" :class="{ active: value == firstOption }">
        <input
          type="radio"
          name="options"
          :value="firstOption"
          @input="$emit('input', $event.target.value)"
          v-model="value"
        />
        {{ firstOption }}
      </label>
      <label class="btn" :class="{ active: value == secondOption }">
        <input
          type="radio"
          name="options"
          :value="secondOption"
          @input="$emit('input', $event.target.value)"
          v-model="value"
        />
        {{ secondOption }}
      </label>
    </div>
  </div>
</template>

<script>
export default {
  name: "Toggle",
  props: {
    value: {
      type: String,
      require: true
    },
    label: {
      type: String,
      require: true
    },
    firstOption: {
      type: String,
      require: true
    },
    secondOption: {
      type: String,
      require: true
    }
  }
};
</script>
<style scoped>
.label {
  color: white;
  padding: 5px 15px 15px 0px;
}
.btn {
  color: white;
}
.btn:not(:disabled) {
  background-color: #424242;
  border-color: #424242;
}
.btn:not(:disabled).active {
  background-color: #f65261;
  border-color: #f65261;
}
</style>
